@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato-regular.woff2") format("woff2"), url("../fonts/lato-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato-semibold.woff2") format("woff2"), url("../fonts/lato-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato-bold.woff2") format("woff2"), url("../fonts/lato-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato-black.woff2") format("woff2"), url("../fonts/lato-black.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 57px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.title {
  font-size: 48px;
  font-weight: 700;
  line-height: 1em;
  color: #000000;
  text-transform: uppercase;
}
.title__blue {
  color: #3e9cdc;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.list__item {
  position: relative;
  padding: 0 0 0 16px;
  line-height: 1em;
}
.list__item::before {
  content: "";
  position: absolute;
  top: 0.6em;
  left: 0;
  line-height: 0;
  height: 2px;
  width: 5px;
  background: #2a2b30;
}

.header {
  position: relative;
  padding: 39px 0 38px;
  background-color: rgba(16, 16, 29, 0.9);
}
.header .container {
  display: flex;
}
.header__logo {
  color: #ffffff;
  margin-right: 20px;
  flex-shrink: 0;
  max-width: 245px;
  max-height: 24px;
  user-select: none;
}
@media (max-width: 767px) {
  .header__logo {
    width: 180px;
    z-index: 1;
  }
}

.main-nav {
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: -8px -2px 0 -60px;
  padding: 5px 0 0;
  list-style: none;
}
.main-nav__item {
  display: block;
  line-height: 1em;
  margin: 8px 0 0 60px;
}
@media (max-width: 767px) {
  .main-nav__item {
    margin-right: 0;
    margin-bottom: 15px;
  }
}
.main-nav__link {
  font-size: 13px;
  font-weight: 600;
  line-height: 1em;
  text-transform: uppercase;
  color: #ffffff;
  transition: opacity 0.3s ease;
  user-select: none;
}
.main-nav__link.is-active {
  opacity: 0.6;
  color: #43abf0;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.3;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.3;
}
.main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    overflow: auto;
    display: none;
  }
}
.main-nav--menu-open {
  z-index: 1;
}
.main-nav--menu-open .main-nav__toggle {
  box-shadow: none;
}
.main-nav--menu-open .main-nav__toggle::after {
  transform: translateY(-50%) rotate(45deg);
}
.main-nav--menu-open .main-nav__toggle::before {
  transform: translateY(-50%) rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--menu-open .main-nav__wrapper {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    padding: 80px 20px 20px;
    background: rgba(16, 16, 29, 0.9);
    width: 100%;
    height: 100vh;
  }
}
.main-nav--menu-open .main-nav__list {
  flex-direction: column;
  margin: 0;
}

.footer {
  font-size: 13px;
  padding: 30px 0;
  background-color: #3d4050;
}

.footer__content {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 1023px) {
  .footer__content {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
}

.footer__copyrite {
  flex-shrink: 0;
  color: #ffffff;
  margin-right: 10px;
}
@media (max-width: 1023px) {
  .footer__copyrite {
    margin-top: 10px;
  }
}

.footer__links {
  display: flex;
  flex-grow: 1;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0 0 0 50px;
}
@media (max-width: 1023px) {
  .footer__links {
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .footer__links {
    flex-direction: column;
  }
}

.footer__payment-link {
  padding: 0 19px 0 0;
  margin-left: 19px;
  margin-top: 5px;
}
@media (max-width: 767px) {
  .footer__payment-link {
    padding-left: 0;
    margin-top: 5px;
  }
}
.footer__payment-link:first-child {
  padding-left: 0;
}

.footer__payment,
.footer__contacts {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .footer__payment,
.footer__contacts {
    flex: 1 0 auto;
    justify-content: flex-start;
  }
}

.footer__payment {
  position: relative;
  justify-content: flex-start;
  padding: 0 28px 0 22px;
  margin-top: -5px;
  margin-left: -19px;
}
@media (max-width: 1023px) {
  .footer__payment {
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .footer__payment {
    padding-bottom: 15px;
    padding-left: 0;
    margin-top: -5px;
  }
}
.footer__payment::after, .footer__payment::before {
  content: "";
  position: absolute;
  height: 63%;
  top: 6px;
  width: 1px;
  background-color: #c3c3c3;
}
@media (max-width: 767px) {
  .footer__payment::after, .footer__payment::before {
    display: none;
  }
}
.footer__payment::before {
  right: 0;
}
.footer__payment::after {
  left: 0;
}
@media (max-width: 1023px) {
  .footer__payment::after {
    display: none;
  }
}
.footer__payment svg {
  margin-top: -4px;
}

.footer__contacts {
  flex: 0 auto;
  padding-left: 10px;
}
@media (max-width: 767px) {
  .footer__contacts {
    padding-bottom: 15px;
    padding-left: 0;
  }
}
.footer__contacts svg {
  margin-top: -4px;
}

.footer__contacts-link:not(:last-child) {
  padding-right: 47px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  position: relative;
  display: inline-flex;
  vertical-align: center;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  letter-spacing: 0.1px;
  line-height: 1.2em;
  text-align: center;
  text-transform: uppercase;
  color: #2a2b30;
  padding: 14px 20px 10px;
  background-color: #ffffff;
  border: solid 1px #6f6d7b;
  border-radius: 4px;
  cursor: pointer;
  isolation: isolate;
  transition: opacity 0.3s ease, color 0.3s ease;
}
.btn::after {
  content: "";
  position: absolute;
  z-index: -1;
  background: #3e9cdc;
  border-radius: 2px;
  inset: 0;
  scale: 0 1;
  transform-origin: right;
  transition: scale 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn--blue {
  color: #ffffff;
  background-color: #3e9cdc;
  border-color: #ffffff;
}
.btn--blue::after {
  background: #ffffff;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    color: #ffffff;
  }
  .btn:hover:not(.focus-visible)::after {
    transform-origin: left;
    scale: 1 1;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  color: #ffffff;
}
.btn.focus-visible:focus::after {
  transform-origin: left;
  scale: 1 1;
}
@media (hover: hover) {
  .btn--blue:hover:not(.focus-visible) {
    color: #2a2b30;
  }
}
.btn--blue.focus-visible:focus {
  color: #2a2b30;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
}
@media (max-width: 1023px) {
  .custom-input,
.custom-textarea {
    font-size: 14px;
  }
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  position: relative;
  display: flex;
  width: 100%;
  background-color: rgba(255, 255, 255, 0);
}
.custom-input label::after,
.custom-textarea label::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: red;
  border-radius: 3px;
  z-index: -1;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 1px solid #ffffff;
  border-radius: 3px;
  outline: none;
  background: rgba(255, 255, 255, 0.85);
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  box-shadow: 0 0 0 2px #ffffff;
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  box-shadow: 0 0 0 2px #ffffff;
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
