  color: #ffffff;
  width: 100px;
  height: 100px;
  background-color: #3e9cdc;
  border-radius: 50%;
  box-shadow: inset 0 0 0 5px #286690;
  margin-bottom: 22px;
}
.stage__icon svg {
  width: 55px;
  height: 55px;
}
@media (max-width: 767px) {
  .stage__icon {
    margin-bottom: 0;
    flex-shrink: 0;
  }
}

.ordering {
  padding: 122px 0 150px;
  background: no-repeat center/cover url("../img/content/form.png"), linear-gradient(#272733, #272733);
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .ordering {
    background: no-repeat center/cover url("../img/content/form@2x.png"), linear-gradient(#272733, #272733);
  }
}
@media (max-width: 1023px) {
  .ordering {
    padding: 80px 0;
  }
}
@media (max-width: 767px) {
  .ordering {
    padding: 60px 0;
  }
}
.ordering__title {
  font-size: 36px;
  font-weight: 900;
  text-align: center;
  color: #ffffff;
  word-spacing: 0.2em;
  margin: 0 0 34px;
}
@media (max-width: 767px) {
  .ordering__title {
    margin: 0 0 0 20px;
    font-size: 24px;
  }
}
.ordering__text {
  font-size: 18px;
  font-weight: 400;
  color: #bbbbbb;
  text-align: center;
  margin: 0 0 78px;
}
@media (max-width: 767px) {
  .ordering__text {
    font-size: 14px;
  }
}

.ordering__stages {
  margin-bottom: 84px;
}

.ordering__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ordering__form-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: flex-start;
  gap: 30px;
  width: 100%;
  margin-bottom: 48px;
}
@media (max-width: 767px) {
  .ordering__form-container {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
}

.ordering__range {
  grid-column: 1/3;
}

.ordering__upload {
  width: 100%;
  padding: 14px 10px 10px;
  border: solid 2px #3e9cdc;
}
.ordering__upload svg {
  display: block;
  height: 21px;
  width: 18px;
  margin-right: 10px;
}
.ordering__upload span {
  font-size: 15px;
  font-weight: 600;
  line-height: 1.3em;
}

.ordering__submit {
  font-size: 15px;
  font-weight: 600;
  max-width: 370px;
  width: 100%;
  padding: 14px;
}

@media (max-width: 767px) {
  .ordering__input + .ordering__input {
    margin-top: 20px;
  }
}

.range {
  position: relative;
}

.range__slider {
  -webkit-appearance: none;
  height: 8px;
  width: 100%;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: inset 0 0 0 1px #ffffff;
}

.range__label {
  display: inline-block;
  font-size: 18px;
  color: #ffffff;
  max-width: 90%;
  width: 100%;
  padding-right: 20px;
  padding-bottom: 8px;
}

.range__value {
  position: absolute;
  right: 0;
  font-size: 18px;
  color: #ffffff;
}

.range__slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 27px;
  height: 27px;
  border: none;
  border-radius: 50%;
  background: #42a9ed;
  box-shadow: inset 0 0 0 3px #286690;
}

.range__slider::-moz-range-thumb {
  -webkit-appearance: none;
  width: 27px;
  height: 27px;
  border: none;
  border-radius: 50%;
  background: #42a9ed;
  box-shadow: inset 0 0 0 3px #286690;
}

